<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>展示系统使用字典类型下的字典信息列表</li>
      <li>可对信息增加、修改、删除操作，为是否系统内置请谨慎操作删除动作</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>字典名称:</label>
        <el-input v-model="queryParams.dictName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>字典类型:</label>
        <el-input v-model="queryParams.dictType" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
    <section class="cms-batch">
      <cms-button v-prem="'/dictionaryType/save'" icon="add" type="success" size="small" @click="$routerLink('/dictionaryType/save','save')">新增</cms-button>
      <cms-button v-prem="'/dictionaryType/delete'" icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="dictName" label="字典名称" min-width="120"></el-table-column>
      <el-table-column prop="dictType" label="字典类型"></el-table-column>
      <el-table-column prop="isSystem" label="是否系统内置" min-width="110" align="center">
        <div slot-scope="scope" :class="[scope.row.isSystem?'green':'red']">{{scope.row.isSystem?'是':'否'}}</div>
      </el-table-column>
      <el-table-column prop="createTime" label="创建时间" min-width="140">
        <template slot-scope="scope">{{$moment(scope.row.createTime)}}</template>
      </el-table-column>
      <el-table-column prop="remark" label="备注" min-width="110">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.remark">{{scope.row.remark}}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作" width='280' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='add' size='small' type='info' @click.stop="$routerLink('/dictionaryType/data','list',{
             dictTypeId:scope.row.id,
            dictType:scope.row.dictType},scope.row.id)">字典数据
          </cms-button>
          <cms-button v-prem="'/dictionaryType/update'" icon='edit' size='small' type='info' @click.stop="$routerLink('/dictionaryType/update','update',{id:scope.row.id},scope.row.id)">编辑</cms-button>
          <cms-button v-prem="'/dictionaryType/delete'" icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  dictTypeManagerPage,
  dictTypeManagerDelete
} from '@/api/setting/dictionary.js'
export default {
  name: 'dictionaryTypeList',
  mixins: [list],
  data() {
    return {
      queryParams: {
        dictName: '',
        dictType: '',
        size: 10,
        page: 1
      },
      type: this.$route.query.type
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(dictTypeManagerDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(dictTypeManagerDelete, this.ids) // 这个才是真实的删除方法
    }
  },

  created() {
    // 首次触发
    this.getTableData(dictTypeManagerPage, this.queryParams)
  }
}
</script>

<style>
</style>



